<template>
  <div class="app-container">
    <el-dialog
        title="部门编辑"
        :visible.sync="addShow"
        width="500px"
        :close-on-click-modal='false'
        >
        <el-form ref="ruleForm" :model="editData" :rules="rules" label-width="80px">
            <el-form-item v-show="editData.parentName" label="上级部门" >
                <el-input clearable  class="dialog_pram" readonly="" v-model="editData.parentName"  />
            </el-form-item>
            <el-form-item label="部门名称" prop="departmentName">
                <el-input clearable class="dialog_pram" placeholder="请输入部门名称" v-model="editData.departmentName"  />
            </el-form-item>
            <el-form-item label="部门简介" prop="introduction">
                <el-input clearable  class="dialog_pram" placeholder="请输入部门简介" v-model="editData.introduction"  />
            </el-form-item>
        </el-form>   
        <span slot="footer">
            <el-button  type="primary"  @click="submitData">确定</el-button>
            <el-button  type=""  @click="cancelUpload">取消</el-button>
        </span>
    </el-dialog>
    <tree-table :data="data" :eval-func="func" :eval-args="args" :expand-all="expandAll" border>
      <el-table-column label="部门名称" align="center" prop="departmentName" />
      <el-table-column label="部门人员" align="center">
        <template slot-scope="scope">
          
        </template>
      </el-table-column>
      <el-table-column label="简介" align="center" prop="introduction" />>
      <el-table-column label="操作" width="250" align="center">
        <template slot-scope="scope">
          <el-button size='mini' type="primary" @click="addData(scope.row)">新增</el-button>
          <el-button size='mini' type="success" @click="putData(scope.row)">编辑</el-button>
          <el-button size='mini' :disabled="scope.row.departmentId==-1?true:false"  type="warning" @click="deleteData(scope.row.departmentId)">删除</el-button>
        </template>
      </el-table-column>
    </tree-table>
  </div>
</template>

<script>
/**
  Auth: Lei.j1ang
  Created: 2018/1/19-14:54
*/
import treeTable from "@/components/TreeTable";
import treeToArray from "./customEval";
import { Permission } from "@/api/permission";

export default {
  components: { treeTable },
  data() {
    return {
      addShow: false,
      func: treeToArray,
      expandAll: false,
      rules: {
        departmentName: [
          { required: true, message: "请输入部门名称", trigger: "change" }
        ],
        introduction: [
          { required: true, message: "请输入部门简介", trigger: "change" }
        ]
      },
      // 需要新增和编辑的数据对象
      editData: {
        parentId: "",
        departmentId: "",
        parentName: "",
        departmentName: "",
        introduction: ""
      },
      data: {},
      args: [null, null, "timeLine"]
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    /**
     * @description 获取数据
     * @function getData
     * @return {type} {description}
     */
    getData() {
      Permission.getDepartment().then(res => {
        this.data = res.data;
      });
    },
    /**
     * @description 数据添加
     * @function addData
     * @return {type} {description}
     */
    addData(obj) {
        this.editData = {
          parentId: obj.departmentId,
          departmentId: "",
          parentName: obj.departmentName,
          departmentName: "",
          introduction: ""
      };
      this.addShow=true;
    },
    /**
     * @description 提交数据
     * @function {function name}
     * @return {type} {description}
     */
    submitData(){
       if(this.editData.parentId) {
         Permission.createDepartment(this.editData).then(res=>{
           this.$message.success('添加成功');
           this.getData();
           this.addShow=false;
         });
       }else{
         Permission.editDepartment(this.editData).then(res=>{
           this.$message.success('编辑成功');
           this.getData();
           this.addShow=false;
         });
       }
    },
    /**
     * @description 数据重置
     * @function cancelUpload
     * @return {type} {description}
     */
    cancelUpload() {
      this.editData = {
        parentId: "",
        departmentId: "",
        parentName: "",
        departmentName: "",
        introduction: ""
      };
      this.addShow=false;
    },
    /**
     * @description 数据编辑
     * @function putData
     * @return {type} {description}
     */
    putData(obj) {
        this.editData = {
          parentId: '',
          departmentId: obj.departmentId,
          parentName: '',
          departmentName: obj.departmentName,
          introduction: obj.introduction
        };
        this.addShow=true;
    },
    /**
     * @description 删除数据
     * @function deleteData
     * @return {type} {description}
     */
    deleteData(departmentId) {
      this.$confirm("确定删除该部门及下属部门?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          Permission.deleteDepartment({
            departmentId
          }).then(res => {
            this.$message({
              type: "success",
              message: "成功删除"
            });
            this.getData();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  }
};
</script>
