import fetchobj from '@/utils/fetch'
let fetch = fetchobj.fetch;

export const Permission = {
   
  getDepartment(data) {
    return fetch('get', '/department', data)
  },

  editDepartment(data) {
    return fetch('put', '/department', data)
  },

  deleteDepartment(data) {
    return fetch('delete', '/department', data)
  },

  createDepartment(data) {
    return fetch('post', '/department', data)
  },


  getUser(data) {
    return fetch('get', '/user', data)
  },

  editUser(data) {
    return fetch('put', '/user', data)
  },

  deleteUser(data) {
    return fetch('delete', '/user', data)
  },

  createUser(data) {
    return fetch('post', '/user', data)
  },

  disabledUser(data) {
    return fetch('post', '/disabledUser', data)
  },
}
